/**
 * @module widget-wm-performance-ng
 *
 * @description
 * Portfolio performance variation widget
 *
 *
 * @example
 *  <div ng-controller="PortfolioPerformanceVariationController as $ctrl">
 *  </div>
 */

import angular from 'vendor-bb-angular';
import { extensionContextKey } from 'lib-bb-widget-extension-ng';
import modelPortfolioSummaryModuleKey, { modelPortfolioSummaryKey }
  from 'model-wm-portfolio-summary-ng';
import stateContainerModuleKey, { bbStateContainerKey } from 'lib-bb-state-container-ng';
import Controller from './controller';
import ViewModel from './view-model';
import Service from './service';

const viewModelKey = 'viewModel';
const serviceKey = 'performanceVariationService';

/**
 * @name default
 * @type {string}
 * @description Angular module name
 */
export default angular
  .module('widget-wm-performance-variation-ng', [
    modelPortfolioSummaryModuleKey,
    stateContainerModuleKey,
  ])

  .factory(viewModelKey, [bbStateContainerKey, ViewModel])
  .factory(serviceKey, [modelPortfolioSummaryKey, viewModelKey, Service])
  .factory(extensionContextKey, [
    serviceKey,
    service => ({ service }),
  ])

  .controller('PortfolioPerformanceVariationController', [
    modelPortfolioSummaryKey,
    viewModelKey,
    serviceKey,
    '$scope',
    /* into */
    Controller,
  ])
  .run([viewModelKey, viewModel => {
    viewModel.init();
  }])
  .name;

/**
 * Portfolio cumulative performance and variation
 * @property {object}        period             - Dates period with from date to date values
 * @property {data[]}        chartData          - Data in format suitable for
 *                                                line chart UI component
 */

/**
 * Data object used to draw line performance chart
 * @typedef {object} data
 * @property {date} dateFrom date from of period
 * @property {date} dateTo date to of period
 * @property {number} value point line chart value
 */

/**
 * Dataset object for period
 * @typedef {object} period
 * @property {date} dateFrom date from of period
 * @property {date} dateTo date to of period
 */



