import { wrapActions } from 'lib-wm-portfolio-view-model-helper';

const today = new Date();
const startOfYear = new Date(today.getFullYear(), 0, 1);
const endOfYear = new Date(new Date(today.getFullYear(), 11, 31).setHours(23, 59, 59, 999));

/**
 * @private
 * @name initialState
 * @description Initial state of widgets store
 * @type {object}
 * @property {object} portfolioPerformance Keeps data and options of portfolio performance
 * @property {object} valuation Keeps data and options of portfolio valuation
 * @property {object} errors Keeps errors is they occur
 * @property {boolean} initialLoading Boolean flag of initial loading
 */
const initialState = {
  portfolioPerformance: {
    data: null,
    loading: false,
    granularity: 'monthly',
    period: {
      dateFrom: startOfYear,
      dateTo: endOfYear,
    },
  },
  valuation: {
    data: null,
    loading: false,
  },
  errors: {
    portfolioPerformanceError: null,
  },
  initialLoading: true,
};

export default (stateContainer) => ({
  getState: stateContainer.getState,
  ...wrapActions(stateContainer, {
    init: (state = initialState) => state,
    beforeLoadPerformanceVariation: () => ({
      portfolioPerformance: {
        loading: true,
      },
    }),
    setPeriod: (state, { dateFrom, dateTo }) => (
      {
        portfolioPerformance: {
          period: {
            dateFrom,
            dateTo,
          },
        },
      }
    ),
    setGranularity: (state, granularity) => ({
      portfolioPerformance: {
        granularity,
      },
    }),
    afterLoadPerformanceSuccess: (state, [portfolioPerformance, valuation]) => ({
      initialLoading: false,
      portfolioPerformance: {
        loading: false,
        data: portfolioPerformance.data,
      },
      valuation: {
        loading: false,
        data: valuation.data,
      },
      errors: {
        portfolioPerformanceError: null,
      },
    }),
    afterLoadPerformanceError: (state, error) => ({
      initialLoading: false,
      portfolioPerformance: {
        loading: false,
        data: null,
      },
      valuation: {
        loading: false,
        data: null,
      },
      errors: {
        portfolioPerformanceError: error.code,
      },
    }),
    setActiveItem: (state, index) => ({
      activeItem: index,
    }),
  }),
});




