export default (model, viewModel) => ({

  /**
   * @description
   * Handles loading performance data
   *
   * @public
   * @name loadPerformanceChart
   * @type {function}
   * @returns {promise.<array>}
   */
  loadPerformanceVariationChart: () => {
    const { granularity, period } = viewModel.getState().portfolioPerformance;

    viewModel.beforeLoadPerformanceVariation();

    return model.getSelectedPortfolioId()
      .then(portfolioId => Promise.all([
        model.loadPerformanceChart(
          portfolioId,
          period.dateFrom,
          period.dateTo
        ),
        model.loadPerformanceValuation(
          portfolioId,
          granularity,
          period.dateFrom,
          period.dateTo
        ),
      ]))
      .then(viewModel.afterLoadPerformanceSuccess)
      .catch(viewModel.afterLoadPerformanceError);
  },
});



